.settings {
  display: flex;
}

.settingsWrapper {
  flex: 9;
  padding: 20px;
}

.settingsTitle {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.settingsUpdateTitle {
  font-size: 30px;
  margin-bottom: 20px;
  color: rgb(88, 64, 64);
}

.settingsPPImg {
  width: 70px;
  height: 70px;
  border-radius: 20%;
  object-fit: cover;
}

.settingsForm {
  display: flex;
  flex-direction: column;
}

.settingsPP {
  display: flex;
  align-items: center;
  margin: 10px 0;
}

.settingsPPIcon {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  color: white;
  background-color: rgb(88, 64, 64);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
  cursor: pointer;
}

.settingsForm > label {
  font-size: 20px;
  margin-top: 20px;
}

.settingsForm > input {
  color: gray;
  margin: 20px 0;
  height: 30px;
  border: none;
  border-bottom: 1px solid rgb(88, 64, 64);
}

.settingsForm > input:focus {
  outline: none;
}

.settingsSubmit {
  width: 150px;
  align-self: center;
  border: none;
  border-radius: 10px;
  color: white;
  background-color: teal;
  padding: 10px;
  margin-top: 20px;
  cursor: pointer;
}
